<template>
  <div class="shoucang">
    <div class="geren">
      <van-nav-bar
        title="我的收藏"
        left-text="返回"
        left-arrow
        @click-left="back"
      />
    </div>
    <div class="zegai"></div>
    <div class="sl">
    <div class="scy" v-for="(item,index) in myscs" :key="index">
        <div class="qbsp">
            <div class="qbtp">
                <img @click="visd(item.pid)" class="tps" :src="item.largeImg">
            </div>
            <div class="d1">{{item.name}}</div>
            <div class="d2">{{item.enname}}</div>
            <div class="d3">￥{{item.price}}</div>
            <div class="tp" @click="del">
                <img src="../assets/img/sc.png" alt="">
            </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Shoucang",

  data() {
    return {
        myscs:[]
    };
  },
  created() {
    this.mysc();
  },

  methods: {
    back() {
      this.$router.go(-1);
    },

    del(myindex){
        // 
        this.myscs.splice(myindex,1)
    },

    //我的收藏
    mysc() {
      let tokenString = localStorage.getItem("tokenString");
      

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code === 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === 2000) {
            this.myscs = res.data.result;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    visd(pid) {
      this.$router.push({name: 'Detail', params: {pid}});
    }
  },
};
</script>

<style lang="less" scoped>
.shoucang {
  .zegai {
    width: 100%;
    height: 120px;
    background: #f6c73d;
  }
  .sl{
      width: 95%;
    //   height: 500px;
      background: white;
      float: left;
      position: relative;
      top: -15px;
      left: 8px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      .scy{
      .qbsp{
          float: left;
          .qbtp{
            .tps{
                width: 100px;
                height: 100px;
                margin-top: 10px;
                margin-left: 15px;
            }
      } 
      }
          
  }
  .d1{
      width: 100px;
      margin-left: 15px;
      font-size: 15px;
      color: #a08ca1;
  }
  .d2{
      width: 100px;
      height: 15px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      margin-left: 15px;
      font-size: 12px;
  }
  .d3{
      width: 100px;
      margin-left: 15px;
      color:#f6c73d;
      font-size: 16px;
  }
  .tp img{
      width: 25px;
      height: 25px;
      float: right;
  }
  }
  
}
</style>